import {getLocalConfigJSonApi} from "../restfulapi/getLocalConfigJson"
import {apiGet, apiPut} from '../../assets/js/baseApi'
import {getSession, checkSession, setSession } from '../../assets/js/storage';

import {servicename, astorename, solutionappname, vncServiceName, repeaterServer, repeaterViewer} from '../../assets/js/constant'

let protocol = "https://";

let repoServerUrl = protocol + window.location.hostname.replace(servicename, astorename);

let solutionappServerUrl = protocol+ window.location.hostname.replace(servicename, solutionappname);

let vncServerUrl = window.location.hostname.replace(servicename, vncServiceName);
let repeaterServerUrl =  protocol + window.location.hostname.replace(servicename, repeaterServer);
let repeaterViewerUrl =  protocol + window.location.hostname.replace(servicename, repeaterViewer);

// let repoServerUrl = "https://portal-apphub-repo-apphub-eks001.axa.wise-paas.com.cn"

// let solutionappServerUrl = "https://solutionapp-androiddm-eks001.hz.wise-paas.com.cn"
