// let repeaterServerUrl = "47.114.102.109"
// let repeaterViewerUrl = "https://repeaterviewer-androiddm-eks001.hz.wise-paas.com.cn"

let getRepeaterServerApi = function(){
    return repeaterServerUrl;  
}

let getRepeaterViewerApi = function(){
    return repeaterViewerUrl
}


let getRepoServerApi = function(){
    return new Promise((resolve, reject) => {
        
        resolve(repoServerUrl);
    })   
}

let getSolutionAppServiceApi = function(){
    return new Promise((resolve, reject) => {
        resolve(solutionappServerUrl);
    }) 
}

        
let getBackendServerUrlApi = function(){
    return new Promise((resolve, reject) => {
        let backendServerUrl="";
        resolve(backendServerUrl);
    })   
}

let getVncServerHostApi = function(){
    return new Promise((resolve, reject) => {
        resolve(vncServerUrl);
    })   
}  



let getServerConfigApi = function(){
    return new Promise((resolve, reject) => {
        apiGet("/api/configmgr/byusername").then((data) =>{
            if(data.data.menu){
                let menudata = JSON.parse(data.data.menu);
                let menu = menudata.menu;
                let osType = menudata.ostype;
                setSession("apphub_menu", menu);
                setSession("apphub_os", osType);
            }
            resolve(data);
        }).catch((error) => {
            resolve(err.response)
        })
    })   
}

let setServerConfigApi = function(data){
    //data={vncip, bloburl,bucket, accesskey, secretkey}
    return new Promise((resolve, reject) => {
        apiPut("/api/configmgr/byusername", data).then((data) =>{
            resolve(data);
        }).catch((error) => {
            resolve(err.response)
        })
    })
}

let setServerConfigByVncIp = function(vncip){
    return new Promise((resolve, reject) => {
        let data = {
            vncip
        }
        apiPut("/api/configmgr/vncip", data).then((data) =>{
            resolve(data);
        }).catch((error) => {
            resolve(err.response)
        })
    })
}

let getQRcodeDataApi = function(){
    return new Promise((resolve, reject) => {
        apiGet('/sso/getQRCodeData').then((data) => {
            resolve(data);
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let getServerIdApi = function(){
    return new Promise((resolve, reject) => {
        apiGet("/api/configmgr/serverId").then((data) =>{
            resolve(data);
        }).catch((error) => {
            resolve(err.response)
        })
    })
}

let getRepoSrcApi = function(){
    return repoServerUrl;
}
let setServerMenuApi = function(username, ostype, menu){
    let menuData = {}
    menuData.ostype  = ostype;
    menuData.menu = menu;
    let menuJson = JSON.stringify(menuData);
    let data = {};
    data.username = username;
    data.menu = menuJson;
    
    return new Promise((resolve, reject) => {
        apiPut("/api/configmgr/setmenubyusername", data).then((data) =>{
            resolve(data);
        }).catch((error) => {
            resolve(err.response)
        })
    })
}

let getServerMenuApi = function(username){
    let data = {username};
    return new Promise((resolve, reject) => {
        apiGet("/api/configmgr/getMenuByUsername", data).then((data) =>{
            resolve(data);
        }).catch((error) => {
            resolve(err.response)
        })
    })
}

export {
    getRepoServerApi,
    getSolutionAppServiceApi, 
    getBackendServerUrlApi,
    getVncServerHostApi,
    getServerConfigApi,
    setServerConfigApi,
    setServerConfigByVncIp,
    getQRcodeDataApi,
    getRepoSrcApi,
    getRepeaterServerApi,
    getRepeaterViewerApi,
    getServerIdApi,
    setServerMenuApi,
    getServerMenuApi
}


