import Vue from 'vue'
import moment from 'moment';
import {workorder} from './lwm2mMap'

export default (function () {
    Vue.filter("threadFilter", (val) => {
        switch (val){
            case "R":
            return "Running";
            case "S":
            return "Sleep";
            case "T":
            return "Stop";
            case "I":
            return "Idle";
            case "Z":
            return "Zombie";
            case "W":
            return "Wait";
            case "L":
            return "Lock";
            default:
            return "";
        }
    })
    Vue.filter("time", (value) => {
        if(value){
            return moment(value).format("YYYY-MM-DD HH:mm:ss")
        }else{
            return "None";
        } 
    })

    Vue.filter("none", (value) =>{
        if(value == null || value == ''){
            return "None";
        } else {
            return value;
        }
    })
    
    Vue.filter("roleFunc", (value) => {
        let val = parseInt(value); 
        switch (val){
            case 0:
            return "Administrator";
            case 1:
            return "Super User";
            case 2:
            return "General User";
            default:
            return "";
        }
    })
    
    Vue.filter("filterEventType", (value) => {
        switch (value){
            case 0:
            return "Info";
            case 1:
            return "Error";
            case 2:
            return "Warning";
            default:
            return "";
        }
       
    })

    Vue.filter("na", (value) =>{
        if(value == null || value == ''){
            return "N/A";
        } else {
            return value;
        }
    })

    Vue.filter("temp", (value) =>{
        if(value == 'N/A'){
            return "N/A";
        }else if(value === ''){
            return '';
        }else {
            return value+"℃";
        }
    })

    Vue.filter("percent", (value) =>{
        if(value == 'N/A'){
            return "N/A";
        }else if(value === ''){
            return '';
        }else {
            return value+"%";
        }
    })

    Vue.filter("gb", (value) =>{
        if(value == 'N/A'){
            return "N/A";
        }else if(value === ''){
            return '';
        }else {
            return value+"G";
        }
    })

    Vue.filter("BSPUpdateStatus", (value) => { 
        switch (value){
            case 0:
            return "Building";
            case 1:
            return "Failed";
            case 2:
            return "Success";
            default:
            return "";
        }
    })

    Vue.filter("BSPErrorStatus", (value) => { 
        value = _g.trim(value);
        if(value){
            switch (value){
                case "0":
                return "None";
                case "1":
                return "Network is not available";
                case "2":
                return "Can not connect to server";
                case "3":
                return "Package verify fail";
                case "4":
                return "Download/Save package error";
                case "5":
                return "Network error";
                case "6":
                return "Package install fail";
                case "7":
                return "Deploy timeout";
                case "8":
                return "Rebirth version check error";
                case "9":
                return "Rebirth status sync error"
                case "10":
                return "Check error at initial phase"
                case "11":
                return "Device is offline"
                case "12":
                return "An application is running"
                
                //server set errorcode 
                case "30":
                return "Check error at initial phase"
                case "31":
                return "An application is running"
                case "32":
                return "Device is offline"
                case "33":
                return "Download file error"
                case "34":
                return "Request agent timeout"
                case "35":
                return "Deploy timeout";
                default:
                return "";
            }
        }else{
            return "";
        }
        
    })

    Vue.filter("appErrorStatus", (value) => { 
        value = _g.trim(value);
        if(value){
            switch (value){
                case "0":
                return "None";
                case "1":
                return "Download file error";
                case "2":
                return "Unknow error";
                case "3":
                return "Apk already exists";
                case "4":
                return "Invalid apk";
                case "5":
                return "Invalid apk path";
                case "6":
                return "Not enough space";
                case "7":
                return "Apk already exists";
                case "8":
                return "User signature error"
                case "9":
                return "User signature error"
                case "10":
                return "System version too old"
                case "11":
                return "System version too new"

                case "12":
                return "Test apk not install"
                case "13":
                return "SD Card access failed"
                case "14":
                return "Invalid install url"
                case "15":
                return "SD card does not exist"
                case "16":
                return "System problem caused fail"
                case "17":
                return "Shared user does not exist"
                case "18":
                return "Shared library error"
                case "19":
                return "Shared library error"
                case "20":
                return "Optimization validation failed"
                case "21":
                return "INSTALL_FAILED_CONFLICTING_PROVIDER"
                case "22":
                return "Contains incompatible code"
                case "23":
                return "An invalid feature was used"
                case "24":
                return "The version not newer"
                //set kiosk
                case "25":
                return "Set kiosk App not found"
                case "26":
                return "Set kiosk App error"
                case "27":
                return "Start App after set kiosk error"
                case "28":
                return "Set kiosk app not in whitelist"
                case "29":
                return "Cancel kiosk Error"
                
                 //server set errorcode 
                case "30":
                return "Check error at initial phase"
                case "31":
                return "An application is running"
                case "32":
                return "Device is offline"
                case "33":
                return "Download file error"
                case "34":
                return "Request timeout"
                case "35":
                return "Task timeout";
                case "36":
                return "Agent not support"
                case "37":
                return "App white list not enable"
                case "40":
                return "Schedule time error"
                case "50":
                return "No permission to store"

                //windows error
                case "101":
                return "Install error";
                case "102":
                return "Uninstall error";
                // linux error
                case "201":
                    return "Install error";
                case "202":
                    return "Uninstall error"
                case "203":
                    return "Decompress failed";
                case "204":
                    return "Uninstall script file not exist";
                case "206":
                    return "Install script file not exist";
                case "208":
                    return "File already exist"
                case "209":
                    return "Invalid";
                case "210":
                    return "Forbidden";
                case "211":
                    return "Conflict";
                default:
                return "";
            }
        }else{
            return "";
        }
        
    })

    Vue.filter("appType", (value) => { 
        value = _g.trim(value);
        if(value){
            switch (value){
                case "system":
                return "System APP";
                case "third":
                return "Third-party APP";
                case "repo":
                return "Repo App"
                default:
                return "";
            }
        }else{
            return "";
        }
        
    })

    Vue.filter("execStatusFunction", (value) => { 
        value = _g.trim(value);
        if(value){
            switch (value){
                case "true":
                return "Enable";
                case "false":
                return "Disable";
                default:
                return "";
            }
        }else{
            return "";
        }
        
    })
    Vue.filter("wdStatusFunction", (value) => { 
        if(value){
            switch (value){
                case workorder.preInstallApp:
                    return "Pre-Install App";
                case workorder.kiosk:
                    return "Set Kiosk";
                case workorder.bootanimation:
                    return "Set Bootanimation";
                case workorder.installDockerCompose:
                    return "Install Docker Compose"
                case workorder.installLinuxPkg:
                    return "Install Linux Package"
                case workorder.installExe:
                    return "Install Exe Package"
                case workorder.installZip:
                    return "Install Windows Zip Package"
                case workorder.installDockerSwarm:
                    return "Install Docker Swarm"
                default:
                    return "";
            }
        }else{
            return ""
        }
        
    })

    Vue.filter("fileType", (value) => {
        if(value){
            switch (value){
                case "tar":
                return "Compress Package";
                case "deb":
                return "Deb Package";
                default:
                return "";
            }
        }else{
            return ""
        }
    })

//appStatus function
    Vue.filter("appStatusFunction", (target) => { 
        let value = _g.trim(target);
        if(value){
            switch (value){
                case "/40004/0/27302":
                    return "Enable App";
                case "/40004/0/27303":
                    return "Disable App";
                case "/40004/0/27306":
                    return "Start App";
                case "/40004/0/27309":
                    return "Stop App";
                case "/40004/0/27308":
                    return "Install App";
                case "/40004/0/27305":
                    return "Uninstall App";
                case "/40004/0/27314":
                    return "Set Kiosk";
                case "/40004/0/27315":
                    return "Cancel Kiosk";
                case "/40004/0/27311":
                    return "Add Whitelist";
                case "/40004/0/27312":
                    return "Remove Whitelist"
                case "/50001/0/27301":
                    return "Install"
                case "/50001/0/27302":
                    return "Uninstall"
                case "/50001/0/27303":
                    return "Upgrade"
                case "/50001/0/28301":
                    return "Install"
                case "/50001/0/28302":
                    return "Uninstall"
                case "/50001/0/28303":
                    return "Upgrade"
                case "/60001/0/27301":
                    return "Install Docker Compose"
                case "/60001/0/27302":
                    return "Uninstall Docker Compose"
                case "/60001/0/27303":
                    return "Install Docker Swarm"
                case "/60001/0/27304":
                    return "Uninstall Docker Swarm"
                case "/60001/0/28301":
                    return "Install"
                case "/60001/0/28302":
                    return "Uninstall"
                case "/60001/0/28303":
                    return "Upgrade"
                case "/60002/0/1017":
                    return "Stop Compose"
                case "/60002/0/1018":
                    return "Start Compose"
                case "/60002/0/1019":
                    return "Restart Compose"
                case "/60002/0/1009":
                    return "Start Container"
                case "/60002/0/1010":
                    return "Stop Container"
                case "/60002/0/1011":
                    return "Restart Container"
                case "/60002/0/1012":
                    return "Kill Container"
                case "/60002/0/1013":
                    return "Pause Container"
                case "/60002/0/1014":
                    return "Resume Container"
                case "/60002/0/1015":
                    return "Remove Container"
                case "/60002/0/1016":
                    return "Remove Image"
                case "/60002/0/1021":
                    return "Force Remove Image"
                case "/60002/0/1032":
                    return "Remove Service"
                case "/60002/0/1037":
                    return "Start Docker Swarm"
                case "/60002/0/1038":
                    return "Restart Docker Swarm"
                case "/60002/0/1036":
                    return "Stop Docker Swarm"
                case "/60001/0/27401":
                    return "Install Docker Swarm"
                case "/60001/0/27402":
                    return "Uninstall Docker Swarm"
                case "/60002/0/1035":
                    return "Update Compose File"
                default:
                return "";
            }
        }else{
            return "";
        }
        
    })
//deviceStatus function
    Vue.filter("deviceStatusFunction", (value) => {
        switch (value){
            case 0:
            return "Normal";
            case 1:
            return "Warning";
            case 2:
            return "Error";
        }
    })
   
})()




