let route = [
    {
        path: '/',
        component: resolve => require(['@/components/account/login'], resolve),
        name: 'login',
        beforeEnter: (to, from, next) => {
            _g.doBeforeLoginout();
            next();
        }
    },
    {
        path: '/main',
        name: 'main',
        component: resolve => require(['@/components/home'], resolve),
        beforeEnter: (to, from, next) => {
            if(cookie.checkCookie("apphubToken")){
                next()
            }else{
                _g.doBeforeLoginout();
                next("/")
            }
        },
        children: childRoute,
        redirect:'/main/overview/overview'    
    },
    {
        path: '*',
        redirect:'/'
    }
    
]

export default route


